import { root } from "postcss";

// initial state
const state = {
  testValues: [],
  testState: 1
};

// getters
const getters = {};

// actions
const actions = {
  testAll({ commit, rootState }, params) {
    console.log(rootState.home.items);
    setTimeout(() => {
      console.log("==== ", params);
      commit("increacetestState", { id: params + 5 });
    }, 300);
  }
};

// mutations
const mutations = {
  setStateValues(state, values) {
    state.testValues = values;
  },

  increacetestState(state, { id }) {
    state.testState += id;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
