import Vue from 'vue'
import Vuex from 'vuex'
import test from './modules/test'
import home from './modules/home'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    test,
    home
  },
  strict: false,
  state: {
    count: 5,
    shareInfo: { count: -1, name: "shenghai" }
  },
  mutations: {
    add(state) {
      state.count++;
    },

    reduce(state) {
      state.count--;
    },

    increase(state, step) {
      state.count = state.count + step;
    },

    reduceByStep(state, step) {
      state.count = state.count - step;
    },
    setShareInfo(state, info) {
      state.shareInfo = info;
    }
  },
  actions: {
    reduceActions(context, step) {
      setTimeout(() => {
        context.commit("reduceByStep", step);
      }, 300);
    }
  },
  getters: {
    share(state) {
      return state.shareInfo;
    }
  }
});
